<template lang="html">
<div class="page has-footer">
  <cube-scroll :options="{scrollbar:{fade: true}}">
    <div class="title-bar">
      <h2>{{obj.Title}}</h2>
      <p>{{obj.Summary}}</p>
    </div>
    <div class="flex bg-white line-top p-md">
      <avatar :src="obj.Avatar" class="mr"></avatar>
      <div class="f-body">
        <div class="fs-sm tc-grey">{{obj.NickName}}</div>
        <p class="desc">财务部</p>
      </div>
    </div>
    <tit more-link="/" has-bg class="mt-md">相关图片</tit>
    <section class="bg-white ph-md">
      <el-row :gutter="10">
        <el-col :span="12" v-for="(item,index) in List" :key="index" v-if="index<2">
          <g-item  :thum-src="item.CoverImage" :title="item.Title"></g-item>
        </el-col>
      </el-row>
    </section>
    <tit more-link="/" has-bg class="mt-md">相关视频</tit>
    <section class="bg-white ph-md">
      <el-row :gutter="10">
        <el-col :span="12" v-for="(item,index) in List" :key="index" v-if="index>2 && index<5">
          <g-item  :thum-src="item.CoverImage" :title="item.Title" is-video></g-item>
        </el-col>
      </el-row>
    </section>
    <tit more-link="/" has-bg class="mt-md">处理信息</tit>
    <section class="bg-white ph-md">
      <ul class="msg-list">

        <li class="flex jc-sb" v-for="(item,index) in List" :key="index + Math.random()" v-if="index<2">
          <avatar :src="item.Avatar" class="mr"></avatar>
          <div class="f-body ">
            <div class="title">{{item.NickName}} <span class="tag ml">信息部</span></div>
            <p class="desc">{{item.UpdateTime}}</p>
          </div>
          <router-link to="/home/xtxx/1" class="btn-lable bg-green ml">协同信息</router-link>
        </li>
        <li class="flex jc-sb" v-for="(item,index) in List" :key="index + Math.random()" v-if="index<2">
          <avatar :src="item.Avatar" class="mr"></avatar>
          <div class="f-body ">
            <div class="title">{{item.NickName}} <span class="tag ml">信息部</span></div>
            <p class="desc">{{item.UpdateTime}}</p>
          </div>
          <router-link to="/home/ddcl/2" class="btn-lable bg-orange ml">调度处理</router-link>
        </li>
      </ul>
    </section>


  </cube-scroll>
  <footer-bar no-icons>
    <div slot="btns">
      <btn type="primary" >提交处理信息</btn>
    </div>
  </footer-bar>
</div>
</template>

<script>
export default {

  data() {
    return {
      List: [],
      obj:{}
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    getListData() {
      this.id = this.$route.params.id
      this.axios.get('/static/data.json').then((res) => {
        this.List = res.data.List
        this.obj = res.data.List[this.id]
      })
    },

  },
}
</script>

<style lang="stylus">
@import "assets/stylus/base"
.msg-list
  li
    align-items: center;
    padding: 10px 0
    + li
      fine-border(top,$border-light)
    .btn-lable
      width: 80px
      text-align: center
      color: $text-dark
      font-size: $font-sm
      background-color: #eee;
      padding: 8px 10px;
    .info-text
      flex: 1
      font-size: $font-sm
      line-height: 1.5
</style>



